.App-header {
    position: relative;
    width: 100%;
    max-width: 100%;
    min-height: 100vh;
    max-height: 100vh;
    overflow: hidden;
}

.App-header > img, .App-header > video {
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    z-index: -2;
    object-fit: cover;
    object-position: center center;
}

.App-header > .isActive {
    z-index: -1;
}

#select-bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    z-index: 1;

    border: aqua 0.1rem solid !important;
    border-radius: 0.5rem !important;
    padding: 0.3rem !important;
}
